.menu-item{
    min-width: 30%;
    height:240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border:1px solid black;
    margin:0 7.5px 15px;
    overflow: hidden;

}
.large{
    height:380px;
}
.menu-item:hover{
    cursor:pointer;
    transform: scale(1.1);
    transition: transform 3s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    opacity:0.9;
}





.background-image{
    width:100%;
    height:100%;
    background-position: center;
    background-size: cover;
}


.content{
    height:115px;
    padding:0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border:1px solid black;
    background-color:white;
    opacity: 0.7;
    position: absolute;
}