.custom-button{
    min-width: 165px;
    width:auto;
    height:50px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding:0 35px 0 35px;
    font-size: 15px;
    background-color: black;
    color:white;
    text-transform: uppercase;
    font-family: 'Open Sans Condensed';
    font-weight: bold;
    border:none;
    cursor: pointer;
    display: flex !important;
  justify-content: center;
  margin-left: 30px;
    margin-top: 40px;
}

.custom-button:hover{
    background-color: White;
    color:black;
    border: 1px solid black;
}

button.google-sign-in.custom-button {
    background-color: #4285f4;
    color: white;
    position: absolute;
    transform: translate(157px, -19px);
}

.custom-button.inverted {
    background-color: white;
    color: black;
    border: 1px solid black;
}
.custom-button.inverted:hover{
    background-color: black;
      color: white;
      border: none;
}
button.google-sign-in.custom-button:hover{
    background-color: #357ae8;
  border:none;
}